import React from 'react';
import PropTypes from 'prop-types';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader';
import connect from '@vkontakte/vk-connect';
import Button from '@vkontakte/vkui/dist/components/Button/Button';
import Group from '@vkontakte/vkui/dist/components/Group/Group';
import Cell from '@vkontakte/vkui/dist/components/Cell/Cell';
import Div from '@vkontakte/vkui/dist/components/Div/Div';
import List from '@vkontakte/vkui/dist/components/List/List';
import Link from '@vkontakte/vkui/dist/components/Link/Link';
import TextArea from '@vkontakte/vkui/dist/components/TextArea/TextArea';

const Home = (props) => (
	<Panel id={props.id}>
		<PanelHeader>{props.fetchedGroup}</PanelHeader>
		
		<Group title="Новая заявка"
		description="Заполните как можно более подробно чтобы мы рассмотрели вашу заявку быстрее.">
			<List>
				<Cell>
					Что хочешь?
					<TextArea id='Commodity'/>
				</Cell>
				<Cell>
					Когда хочешь?
					<TextArea id='CommodityEndDate'/>
				</Cell>
				<Cell>
					Пожелания к заказу
					<TextArea id='CommodityDescription'/>
				</Cell>	
			</List>
			<Div>
				<Button size="xl" level="2" onClick={props.go} data-to="GoodRequest">
					Отправить
				</Button>
			</Div>
		</Group>
			{<Div>
				<Button size="xl" level="2" onClick={connect.sendPromise("VKWebAppJoinGroup", {"group_id": 139136005})
				.then(data => { if (data.result===true){}})}>
					Вступить в группу
				</Button>
			</Div>}
			{(connect.send("VKWebAppCallAPIMethod", 
			{"method": "groups.get", "request_id": "GroupsButtons",
			 "params": {"param_extended": "1","param_user_id": props.fetchedUser.id,"param_filter": "admin, editor, moder", "v":"5.103", "access_token":props.token}})
			 ).response.Where(i => i.id == props.groupId).from(Array).lenght>0
			&&
			<Div>
				<Button size="xl" level="outline" onClick={props.go} data-to="Requests">
					Заявки
				</Button>
			</Div>}
			{<Div>
				<Button size="xl" level="commerce" onClick={props.go} data-to="Accounting">
					Бухгалтерия
				</Button>
			</Div>}
			{<Div>
				<Button size="xl" level="destructive" onClick={props.go} data-to="Payments">
					Оплаты
				</Button>
			</Div>}
	</Panel>
);

Home.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
	fetchedUser: PropTypes.shape({
		photo_200: PropTypes.string,
		id: PropTypes.number.isRequired,
		first_name: PropTypes.string,
		last_name: PropTypes.string,
		city: PropTypes.shape({
			title: PropTypes.string,
		}),
	}),
	fetchedGroup: PropTypes.string.isRequired,
	token: PropTypes.shape({
		access_token: PropTypes.string,
		scope: PropTypes.string,
	}),
	groupId: PropTypes.number.isRequired,
};

export default Home;
